@import "https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400&display=swap";
body {
  height: 100vh;
  color: #fff;
  box-sizing: border-box;
  -webkit-user-select: none;
  user-select: none;
  justify-content: center;
  align-items: center;
  font-family: Inter;
  display: flex;
}

.container {
  height: 320px;
  width: 230px;
  background-color: #545556;
  border: 1px solid #0000001a;
  border-radius: 12px;
  flex-direction: column;
  display: flex;
  overflow: hidden;
  box-shadow: 1px 15px 40px #000000b3;
}

.topbar {
  color: #0000;
  padding: 9px 9px 0;
  display: flex;
}

.topbar-buttons-container {
  display: flex;
}

.topbar-buttons-container:hover {
  color: #0006;
  cursor: default;
  display: flex;
}

.bar-button {
  width: 12px;
  height: 12px;
  background-color: #fff;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  margin-right: 8px;
  font-size: 11pt;
  display: flex;
}

.bar-button > span {
  position: relative;
  bottom: 1px;
}

.bar-button.bar-button-red {
  background-color: #ff5f57;
}

.bar-button.bar-button-yellow {
  background-color: #ffbc2e;
  font-size: 15pt;
}

.bar-button.bar-button-green {
  background-color: #2bc840;
}

#display {
  color: #fff;
  text-align: right;
  letter-spacing: -3px;
  height: 55px;
  padding-top: 10px;
  padding-bottom: 0;
  padding-right: 14px;
  font-family: Courier New, Courier, monospace;
  font-size: 32pt;
  font-weight: 200;
}

.buttons-container {
  flex-direction: column;
  flex-grow: 1;
  display: flex;
}

.button-row {
  flex: 1;
  display: flex;
}

.button-row:not(:last-child) {
  border-bottom: 1px solid #555;
}

.calc-button {
  color: #fff;
  border: none;
  flex: 1;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  font-family: Inter;
  font-weight: 200;
  display: flex;
}

.calc-button:not(:last-child) {
  border-right: 1px solid #555;
}

.calc-button.calc-button-dark {
  background-color: #656667;
  font-size: 14pt;
  font-weight: 300;
}

.calc-button.calc-button-dark:active, .calc-button.calc-button-dark.active {
  background-color: #878585;
}

.calc-button.calc-button-orange {
  background-color: #ff9e0b;
  font-size: 20pt;
  font-weight: 300;
}

.calc-button.calc-button-orange:active, .calc-button.calc-button-orange.active {
  background-color: #cb7e07;
}

.calc-button.calc-button-light {
  background-color: #7e7f80;
  font-size: 16pt;
}

.calc-button.calc-button-light:active, .calc-button.calc-button-light.active {
  background-color: #b9b9b8;
}

.zero-button {
  flex: 2;
  padding-right: 1px;
}

.calc-button-selected > .op-border {
  width: 100%;
  height: 96%;
  border: 1px solid #555;
  justify-content: center;
  align-items: center;
  display: flex;
}

